html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}


body {
  background-color: $color-snow;
  color: $color-primary;
  font-family: $font-primary;
  font-size: 16px;
  line-height: 1.5;
}

h1, h2, h3, h4, h5, h6 {
  font-family: $font-secondary;
  font-weight: 600;
}

h1 {
  margin-bottom: 24px;
  padding-top: 70px;
  color: $color-white;
  font-size: 42px;
  font-weight: 600;
  line-height: 50px;
}

h2 {
  position: relative;
  margin: 46px 0 32px;
  font-size: 28px;
  line-height: 32px;

  &:after {
    position: absolute;
    bottom: -10px;
    left: 0;
    width: 40px;
    height: 4px;
    display: block;
    border-radius: 4px;
    content: '';
    background-color: $color-green;
  }
}

h3,
summary {
  margin: 22px 0 8px;
  font-size: 20px;
  font-weight: 700;
  line-height: 32px;
}

h4 {
  margin: 10px 0;
  font-size: 18px;
  line-height: 24px;
}

img {
  max-width: 100%;
}

p {
  font-size: 16px;
  line-height: 24px;

  & + p {
    margin-top: 8px;
  }

  & + ol,
  & + ul,
  & + figure {
    margin-top: 16px;
  }
}

strong {
  font-weight: 700;
}

em {
  font-style: italic;
}

a {
  color: $color-ocean;
  font-family: $font-secondary;
  font-weight: 600;

  &:hover,
  &:focus {
    color: $color-ocean--deep;
  }
}

[tabindex]:not([tabindex="-1"]),
a,
button,
input,
select,
textarea {
  outline: 2px solid transparent;
  outline-color: transparent;
  outline-offset: 5px;
  transition: outline-offset $transition-duration $transition-effect;

  &:focus {
    outline-color: $color-ocean--deep;
    outline-offset: 1px;
  }
}

ul {
  position: relative;
  list-style-type: none;

  li {
    position: relative;

    &:before {
      position: absolute;
      top: 10px;
      margin-left: -16px;
      height: 6px;
      width: 6px;
      border: 1px solid $color-dark;
      border-radius: 50%;
      display: inline-block;
      background-color: $color-dark;
      content: '';
    }
  }

  ul {
    li:before {
      border-color: $color-green;
      border-radius: 50%;
      background-color: $color-white;
    }

    ul {
      li:before {
        border-color: $color-green;
        border-radius: 0;
        background-color: $color-white;
      }
    }
  }
}

ol,
ul {
  padding-left: 16px;

  li {
    margin-bottom: 8px;

    p {
      margin: 0;
    }
  }

  & ol,
  & ul {
    margin-top: 8px;
  }
}

details {
  summary {
    &::-webkit-details-marker {
      display:none;
    }
  }

  & > summary {
    &:first-of-type {
      list-style-type: none;
    }
  }

  &[open] {
    p {
      padding-left: 50px;
    }

    ul {
      padding-left: 66px;

      ul {
        padding-left: 24px;
      }
    }

    & > summary {
      p {
        padding-left: 0;
      }

      &:before {
        content: "–";
        line-height: 29px;
      }
    }
  }
}

summary {
  display: flex;
  align-items: center;
  cursor: pointer;

  &:before {
    margin-right: 8px;
    width: 40px;
    height: 40px;
    border: 1px solid $color-green;
    border-radius: 10px;
    display: inline-block;
    background: $color-white;
    color: $color-green;
    content: "+";
    font-size: 50px;
    font-weight: 400;
    line-height: 37px;
    text-align: center;
    transition: background $transition-duration $transition-effect, color $transition-duration $transition-effect;
  }

  &:focus,
  &:hover {
    &:before {
      background: $color-green;
      color: $color-white;
    }
  }
}

figure {
  //width: min-content;
  max-width: 100%;

  p {
    margin: 0;
  }

  img {
    max-width: none;
    border: 1px solid $color-silver;
  }

  & + p {
    margin-top: 16px;
  }

  @media (--tablet){
    img {
      width: auto;
    }
  }
}

figcaption {
  margin: 0;
  font-style: italic;
  font-weight: 400;
}

code {
  font-family: $font-code;
  font-size: 14px;
}

/* global classes */

.content {
  position: relative;
}

.page {
  padding-bottom: 68px;
}

.wrapper {
  @include container;
}

.box {
  margin-top: 16px;
  border: 1px solid $color-silver;
  background-color: $color-white;

  & + .box {
    margin-top: 0;
    border-top: 0;
  }
}

.figure--double {
  @media (--mobileL){
    p {
      display: flex;
    }
  }
}

.figure--tripple {
  @media (--tabletS){
    p {
      display: flex;
      align-items: flex-start;
    }
  }
}

table {
  margin-top: 16px;
  border: solid 1px $color-silver;
  border-collapse: collapse;
  border-spacing: 0;

  thead th {
    background-color: $color-paper;
    border: solid 1px $color-silver;
    color: $color-dark;
    padding: 10px;
    font-size: 14px;
    font-weight: 600;
    text-align: left;
  }

  tbody td {
    border: solid 1px $color-silver;
    color: $color-dark;
    padding: 10px;
  }
}
