@charset "UTF-8";
/*style*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

@custom-media --mobileS (min-width: 320px);
@custom-media --mobileM (min-width: 375px);
@custom-media --mobileL (min-width: 465px);
@custom-media --tabletS (min-width: 600px);
@custom-media --tablet (min-width: 768px);
@custom-media --tabletL (min-width: 860px);
@custom-media --laptop (min-width: 1024px);
@custom-media --laptopL (min-width: 1560px);
@custom-media --fourK (min-width: 2560px);
@custom-media --maxMobile (max-width: 767px);
@lost gutter 12px;
@font-face {
	font-family: 'Source Sans Pro';
	src: url("assets/fonts/source-sans-pro/SourceSansPro-Regular.woff") format("woff");
	font-weight: 400;
	font-style: normal;
}

@font-face {
	font-family: 'Source Sans Pro';
	src: url("assets/fonts/source-sans-pro/SourceSansPro-Italic.woff") format("woff");
	font-weight: 400;
	font-style: italic;
}

@font-face {
	font-family: 'Source Sans Pro';
	src: url("assets/fonts/source-sans-pro/SourceSansPro-Bold.woff") format("woff");
	font-weight: 700;
	font-style: normal;
}

@font-face {
	font-family: 'Source Sans Pro';
	src: url("assets/fonts/source-sans-pro/SourceSansPro-BoldItalic.woff") format("woff");
	font-weight: 700;
	font-style: italic;
}

@font-face {
	font-family: 'Barlow';
	src: url("assets/fonts/barlow/Barlow-Regular.woff") format("woff");
	font-weight: 400;
	font-style: normal;
}

@font-face {
	font-family: 'Barlow';
	src: url("assets/fonts/barlow/Barlow-Italic.woff") format("woff");
	font-weight: 400;
	font-style: italic;
}

@font-face {
	font-family: 'Barlow';
	src: url("assets/fonts/barlow/Barlow-SemiBold.woff") format("woff");
	font-weight: 600;
	font-style: normal;
}

@font-face {
	font-family: 'Barlow';
	src: url("assets/fonts/barlow/Barlow-Bold.woff") format("woff");
	font-weight: 700;
	font-style: normal;
}

@font-face {
	font-family: 'Barlow';
	src: url("assets/fonts/barlow/Barlow-BoldItalic.woff") format("woff");
	font-weight: 700;
	font-style: italic;
}

@font-face {
	font-family: 'Source Code Pro';
	src: url("assets/fonts/source-code-pro/SourceCodePro-Regular.woff") format("woff");
	font-weight: 400;
	font-style: normal;
}

html {
	box-sizing: border-box;
}

*, *:before, *:after {
	box-sizing: inherit;
}

body {
	background-color: #fafafc;
	color: #2C2C2C;
	font-family: "Source Sans Pro", sans-serif;
	font-size: 16px;
	line-height: 1.5;
}

h1, h2, h3, h4, h5, h6 {
	font-family: "Barlow", sans-serif;
	font-weight: 600;
}

h1 {
	margin-bottom: 24px;
	padding-top: 70px;
	color: #ffffff;
	font-size: 42px;
	font-weight: 600;
	line-height: 50px;
}

h2 {
	position: relative;
	margin: 46px 0 32px;
	font-size: 28px;
	line-height: 32px;
}

h2:after {
	position: absolute;
	bottom: -10px;
	left: 0;
	width: 40px;
	height: 4px;
	display: block;
	border-radius: 4px;
	content: '';
	background-color: #3C8200;
}

h3,
summary {
	margin: 22px 0 8px;
	font-size: 20px;
	font-weight: 700;
	line-height: 32px;
}

h4 {
	margin: 10px 0;
	font-size: 18px;
	line-height: 24px;
}

img {
	max-width: 100%;
}

p {
	font-size: 16px;
	line-height: 24px;
}

p + p {
	margin-top: 8px;
}

p + ol,
p + ul,
p + figure {
	margin-top: 16px;
}

strong {
	font-weight: 700;
}

em {
	font-style: italic;
}

a {
	color: #046BC4;
	font-family: "Barlow", sans-serif;
	font-weight: 600;
}

a:hover, a:focus {
	color: #00427E;
}

[tabindex]:not([tabindex="-1"]),
a,
button,
input,
select,
textarea {
	outline: 2px solid transparent;
	outline-color: transparent;
	outline-offset: 5px;
	transition: outline-offset 0.3s linear;
}

[tabindex]:not([tabindex="-1"]):focus,
a:focus,
button:focus,
input:focus,
select:focus,
textarea:focus {
	outline-color: #00427E;
	outline-offset: 1px;
}

ul {
	position: relative;
	list-style-type: none;
}

ul li {
	position: relative;
}

ul li:before {
	position: absolute;
	top: 10px;
	margin-left: -16px;
	height: 6px;
	width: 6px;
	border: 1px solid #1d1d1d;
	border-radius: 50%;
	display: inline-block;
	background-color: #1d1d1d;
	content: '';
}

ul ul li:before {
	border-color: #3C8200;
	border-radius: 50%;
	background-color: #ffffff;
}

ul ul ul li:before {
	border-color: #3C8200;
	border-radius: 0;
	background-color: #ffffff;
}

ol,
ul {
	padding-left: 16px;
}

ol li,
ul li {
	margin-bottom: 8px;
}

ol li p,
ul li p {
	margin: 0;
}

ol ol,
ol ul,
ul ol,
ul ul {
	margin-top: 8px;
}

details summary::-webkit-details-marker {
	display: none;
}

details > summary:first-of-type {
	list-style-type: none;
}

details[open] p {
	padding-left: 50px;
}

details[open] ul {
	padding-left: 66px;
}

details[open] ul ul {
	padding-left: 24px;
}

details[open] > summary p {
	padding-left: 0;
}

details[open] > summary:before {
	content: "–";
	line-height: 29px;
}

summary {
	display: flex;
	align-items: center;
	cursor: pointer;
}

summary:before {
	margin-right: 8px;
	width: 40px;
	height: 40px;
	border: 1px solid #3C8200;
	border-radius: 10px;
	display: inline-block;
	background: #ffffff;
	color: #3C8200;
	content: "+";
	font-size: 50px;
	font-weight: 400;
	line-height: 37px;
	text-align: center;
	transition: background 0.3s linear, color 0.3s linear;
}

summary:focus:before, summary:hover:before {
	background: #3C8200;
	color: #ffffff;
}

figure {
	max-width: 100%;
}

figure p {
	margin: 0;
}

figure img {
	max-width: none;
	border: 1px solid #dddddd;
}

figure + p {
	margin-top: 16px;
}

@media (--tablet) {
	figure img {
		width: auto;
	}
}

figcaption {
	margin: 0;
	font-style: italic;
	font-weight: 400;
}

code {
	font-family: "Source Code Pro", sans-serif;
	font-size: 14px;
}

/* global classes */
.content {
	position: relative;
}

.page {
	padding-bottom: 68px;
}

.wrapper {
	width: 90%;
	margin: 0 auto;
	transition: width .2s ease-in-out;
}

.wrapper::after {
	clear: both;
	content: "";
	display: table;
}

@media (--laptop) {
	.wrapper {
		width: 880px;
		margin: 0 auto;
	}
	.wrapper::after {
		clear: both;
		content: "";
		display: table;
	}
}

.box {
	margin-top: 16px;
	border: 1px solid #dddddd;
	background-color: #ffffff;
}

.box + .box {
	margin-top: 0;
	border-top: 0;
}

@media (--mobileL) {
	.figure--double p {
		display: flex;
	}
}

@media (--tabletS) {
	.figure--tripple p {
		display: flex;
		align-items: flex-start;
	}
}

table {
	margin-top: 16px;
	border: solid 1px #dddddd;
	border-collapse: collapse;
	border-spacing: 0;
}

table thead th {
	background-color: #f2f2f2;
	border: solid 1px #dddddd;
	color: #1d1d1d;
	padding: 10px;
	font-size: 14px;
	font-weight: 600;
	text-align: left;
}

table tbody td {
	border: solid 1px #dddddd;
	color: #1d1d1d;
	padding: 10px;
}

.button {
	position: relative;
	padding: 8px 16px;
	border: 1px solid #3C8200;
	border-radius: 10px;
	display: inline-block;
	background-color: #3C8200;
	color: #ffffff;
	cursor: pointer;
	font-family: "Barlow", sans-serif;
	font-size: 16px;
	font-weight: 600;
	line-height: 24px;
	text-decoration: none;
}

.button.is-visible {
	opacity: 1;
	visibility: visible;
}

.button--backtotop {
	position: fixed;
	right: 16px;
	bottom: 16px;
	border-radius: 100px;
	border-color: #3C8200;
	background-color: #3C8200;
	box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
	opacity: 0;
	visibility: hidden;
	transition: all .5s linear;
}

.button.button--secondary {
	margin-right: 12px;
	padding: 8px 16px 8px 48px;
	background-color: #ffffff;
	color: #3C8200;
}

.button.button--secondary span {
	position: absolute;
	top: 4px;
	left: 16px;
	font-size: 40px;
	font-weight: 400;
}

.button.button--secondary:hover {
	color: #ffffff;
	border-color: #3C8200;
	background-color: #3C8200;
}

.button.button--secondary:focus {
	color: #3C8200;
}

.button.button--secondary:focus:hover {
	color: #ffffff;
}

.button--small {
	padding: 2px 4px;
}

.button:hover, .button:focus {
	color: #ffffff;
}

.button:focus {
	outline-color: #3C8200;
}

.button svg {
	width: 16px;
	height: 16px;
	display: inline-block;
	fill: #ffffff;
}

.button:disabled {
	background-color: #3C8200;
	color: #ffffff;
	opacity: .75;
	pointer-events: none;
}

.button:disabled:focus {
	background-color: #3C8200;
	color: #ffffff;
}

.button:disabled span {
	top: 5px;
}

.footer {
	padding: 30px 0;
	background-color: #2C2C2C;
}

.footer a {
	margin: 0 auto;
	display: block;
}

.footer a + a {
	margin-top: 24px;
}

@media (--tabletS) {
	.footer__content {
		margin: 0 auto;
		max-width: 340px;
		display: flex;
		align-items: center;
	}
	.footer a + a {
		margin-top: 0;
	}
}

@media (--tablet) {
	.footer__content {
		max-width: 440px;
	}
}

.intro {
	position: relative;
	overflow: hidden;
}

.intro--hero {
	padding-bottom: 80px;
}

.intro--hero:before, .intro--hero:after {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	content: '';
}

.intro--hero:before {
	z-index: -1;
	background-color: rgba(33, 33, 33, 0.65);
}

.intro--hero:after {
	z-index: -2;
	background-image: url("assets/images/intro-background.jpg");
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}

.intro--hero p {
	color: #ffffff;
	font-size: 18px;
}

.intro--middle {
	padding-bottom: 46px;
}

.intro--page-content {
	padding-bottom: 30px;
	background-color: #f2f2f2;
}

.intro__buttons {
	margin: 24px 0;
}

.intro__head {
	background-color: #fafafc;
}

.intro header {
	padding: 8px 16px 2px;
	font-size: 20px;
	font-weight: 700;
	line-height: 28px;
}

.intro header.intro__head {
	padding: 8px 16px;
}

.intro header h3 {
	margin: 0;
	font-size: 16px;
}

.intro ol, .intro ul {
	padding-left: 40px;
}

.intro ol ol, .intro ol ul, .intro ul ol, .intro ul ul {
	padding-left: 24px;
}
