.footer {
  padding: 30px 0;
  background-color: $color-primary;

  a {
    margin: 0 auto;
    display: block;

    & + a {
      margin-top: 24px;
    }
  }

  @media (--tabletS) {
    &__content {
      margin: 0 auto;
      max-width: 340px;
      display: flex;
      align-items: center;
    }

    a {
      & + a {
        margin-top: 0;
      }
    }
  }

  @media (--tablet) {
    &__content {
      max-width: 440px;
    }
  }
}
