.button {
  position: relative;
  padding: 8px 16px;
  border: 1px solid $color-green;
  border-radius: 10px;
  display: inline-block;
  background-color: $color-green;
  color: $color-white;
  cursor: pointer;
  font-family: $font-secondary;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  text-decoration: none;

  &.is-visible {
    opacity: 1;
    visibility: visible;
  }

  &--backtotop {
    position: fixed;
    right: 16px;
    bottom: 16px;
    border-radius: 100px;
    border-color: $color-green;
    background-color: $color-green;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.5);
    opacity: 0;
    visibility: hidden;
    transition: all .5s linear;
  }

  &.button--secondary {
    margin-right: 12px;
    padding: 8px 16px 8px 48px;
    background-color: $color-white;
    color: $color-green;

    span {
      position: absolute;
      top: 4px;
      left: 16px;
      font-size: 40px;
      font-weight: 400;
    }

    &:hover {
      color: $color-white;
      border-color: $color-green;
      background-color: $color-green;
    }

    &:focus {
      color: $color-green;

      &:hover {
        color: $color-white;
      }
    }
  }

  &--small{
    padding: 2px 4px;
  }

  &:hover,
  &:focus {
    color: $color-white;
  }

  &:focus {
    outline-color: $color-green;
  }

  svg {
    width: 16px;
    height: 16px;
    display: inline-block;
    fill: $color-white;
  }

  &:disabled {
    background-color: $color-green;
    color: $color-white;
    opacity: .75;
    pointer-events: none;

    &:focus {
      background-color: $color-green;
      color: $color-white;
    }

    span {
      top: 5px;
    }
  }
}
