.intro {
  position: relative;
  overflow: hidden;

  &--hero {
    padding-bottom: 80px;

    &:before,
    &:after {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      content: '';
    }

    &:before {
      z-index: -1;
      background-color: rgba(33,33,33,.65);
    }

    &:after {
      z-index: -2;
      background-image: url("assets/images/intro-background.jpg");
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
    }

    p {
      color: $color-white;
      font-size: 18px;
    }
  }

  &--middle {
    padding-bottom: 46px;
  }

  &--page-content {
    padding-bottom: 30px;
    background-color: $color-paper;
  }

  &__buttons {
    margin: 24px 0;
  }

  &__head {
    background-color: $color-snow;
  }

  header {
    padding: 8px 16px 2px;
    font-size: 20px;
    font-weight: 700;
    line-height: 28px;

    &.intro__head {
      padding: 8px 16px;
    }

    h3 {
      margin: 0;
      font-size: 16px;
    }
  }

  ol, ul {
    padding-left: 40px;

    ol, ul {
      padding-left: 24px;
    }
  }
}
